<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>身份验证</h1>
            </v-header>
        </div>
        <div class="check-container">
            <div class="cell-wrap">
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">卡号</span>
                    <input type="tel"
                           placeholder="请输入卡号"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.bankCardNo"
                           maxlength="20"
                    >
                    <div class="icon-wrap wenhao" slot="right">
                        <i class="icon icon-scan"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">证件类型</span>
                    <input type="text"
                           placeholder="请选择证件类型"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.cerType"
                           readonly
                    >
                    <div class="icon-wrap arrow" slot="right">
                        <i class="icon icon-arrow-bottom"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">证件号码</span>
                    <input type="tel"
                           placeholder="请输入证件号码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.certificateNum"
                           maxlength="20"
                    >
                    <div class="icon-wrap camera" slot="right">
                        <i class="icon icon-xiangji"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">验证码</span>
                    <input type="tel"
                           placeholder="请输入验证码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.checkNum"
                           maxlength="6"
                    >
                    <div class="check-num-btn" slot="right">
                        <button class="btn" :disabled="checkNumCutTime !== ''" @click="sendCheckNum">
                            <span>{{checkNumCutTime}}</span><span v-show="checkNumCutTime">s</span><span>{{checkNumTxt}}</span>
                        </button>
                    </div>
                </v-cell-box>
                <v-cell-box>
                    <span class="title" slot="left">取款密码</span>
                    <input type="password"
                           placeholder="请输入取款密码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.pwd"
                           maxlength="20"
                    >
                </v-cell-box>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="下一步" :disabled="!canNext" @click="jumpSetPwdPage"></v-linear-button>
            </div>
        </div>
        <v-toast v-model="showToast"
                 :toastTxt="toastTxt"
        ></v-toast>
    </v-page>
</template>

<script>
    import {vCellBox} from '../../component/cell'
    import {vLinearButton} from '../../component/linearButton'
    export default {
        name: 'checkIdentity',
        components: {
            vCellBox,
            vLinearButton
        },
        computed: {
            // 能否点击下一步
            canNext () {
                return this.forgetInfo.bankCardNo !== '' &&
                        this.forgetInfo.cerType !== '' &&
                        this.forgetInfo.certificateNum !== '' &&
                        this.forgetInfo.pwd !== '' &&
                        this.forgetInfo.checkNum !== ''
            }
        },
        data () {
            return {
                forgetInfo: {
                    phone: '',             // 手机号
                    bankCardNo: '',        // 银行卡号
                    cerType: '身份证',     // 证件类型
                    certificateNum: '',    // 证件号码
                    pwd: '',               // 取款密码
                    checkNum: ''           // 短信验证码
                },
                // toast提示框信息
                toastTxt: '',
                // 是否显示toast提示框
                showToast: false,
                // 发送验证码按钮文本
                checkNumTxt: '获取验证码',
                // 发送倒计时
                checkNumCutTime: '',
                // 验证码定时器对象
                checkNumTimer: null,
            }
        },
        methods: {
            // 跳转到设置密码页面
            jumpSetPwdPage () {
                let jumpUrl = '/login/forgetPwd/setPwd'
                let params = {
                    hasBindCard: true
                }
                window.T.go({
                    path: jumpUrl,
                    params
                })
            },
            // 发送短信验证码
            sendCheckNum () {
                // 校验手机号
                let isPhoneNum = window.T.Validator.phone(this.forgetInfo.phone, '手机号')
                if (isPhoneNum !== true) {
                    this.toastTxt = isPhoneNum
                    this.showToast = true
                    return
                }
                this._checkNumCutTime('checkNumTxt','checkNumCutTime', 'checkNumTimer')
            },
            // 短信验证码倒计时
            _checkNumCutTime (btnTxtKey, timeKey, timerKey) {
                this[timeKey] = '60'
                let oldBtnTxt = this[btnTxtKey]
                this[btnTxtKey] = '重新发送'
                this[timerKey] = setInterval(() => {
                    this[timeKey] --
                    if (this[timeKey] < 1) {
                        this[timeKey] = ''
                        this[btnTxtKey] = oldBtnTxt
                        clearInterval(timer)
                    }
                }, 1000)
            },
            // 获取上个页面传递的参数
            getPageParams () {
                window.T.Native.getPageParams().then((ret) => {
                    this.forgetInfo = Object.assign(this.forgetInfo, ret.params)
                })
            }
        },
        created () {
            this.getPageParams()
        }
    }
</script>

<style lang="scss" scoped>
    .check-container{
        padding-top: px2rem(20);
        padding-bottom: px2rem(40);
        .cell-wrap {
            font-size: px2rem(32);
            flex: none;
            span {
                display: block;
            }
            .title {
                width: px2rem(230);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
            .check-num-btn {
                color: #F28300;
                font-family: PingFangSC-Medium;
                margin-left: px2rem(10);
                font-size: px2rem(28);
                line-height: px2rem(40);
                span{
                    display: inline;
                }
                .btn {
                    background-color: #ffffff;
                    border: none;
                    height: 1rem;
                    padding: 0 .2rem;
                    color: #F28300;
                    @include font-color(#F28300);
                    &[disabled]{
                        color: #dcbf9d;
                    }
                }
            }
            .icon-wrap {
                width: px2rem(48);
                height: px2rem(48);
                display: flex;
                align-items: center;
                justify-content: flex-end;
                color: #9B9B9B;
                .icon {
                    width: px2rem(48);
                    height: px2rem(48);
                }
                .icon-xiangji {
                    font-size: px2rem(40);
                    line-height: px2rem(48);
                }
                .icon-scan {
                    font-size: px2rem(40);
                    width: px2rem(40);
                    height:px2rem(40);
                }
                &.arrow {
                    .icon {
                        width: px2rem(24);
                        height: px2rem(24);
                    }
                }
            }
        }
        .btn-wrap{
            padding: 0 px2rem(40);
            margin-top: px2rem(80);
        }
    }
</style>



